import * as React from "react"
import { Link } from 'gatsby';
import '../styles/index.css';

const Sidebar = () => {
  return (
    <div id="sidebar">
        <h3>
            <Link to='/'>
                home
            </Link>
        </h3>
        <h3>
            <Link to='/about'>
                about
            </Link>
        </h3>
        <h3>
            <Link to='/404'>
                404
            </Link>
        </h3>
    </div>
  )
}

export default Sidebar;
