import * as React from "react"
import '../styles/index.css';

const Button = ({ label, onClick }: buttonProps) => {
	return (
        <button
            className="button"
            onClick={onClick}
        >
            <p>
                {label}
            </p>
        </button>
	)
}

interface buttonProps {
    label: string;
    onClick: () => void;
}

export default Button;
